* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #00B293;
  color: #fff;
  font-family: 'Poppins', sans-serif;
  font-size: 100%;
}

button:hover {
  background: #ddd;
}

button:disabled {
  cursor: not-allowed;
}

.container {
  border: 3px solid rgb(233, 231, 231);
  border-radius: 10px;
  color: #444;
  background: #fff;
  width: 50%;
  margin: 5rem auto;
  padding: 1rem 4rem;
}

.title-head {
  text-align: center;
  margin-bottom: 2rem;
}

.container-divs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 4rem 2rem;
}

.length-div {
  border: 1px solid #444;
  padding: 1.5rem 1rem 2rem 1rem;
  border-radius: 3px;
}

.length-head {
  font-weight: 500;
  text-align: center;
  font-size: 110%;
}

.length-body {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: .5rem;
}

.length-time {
  padding-left: 1rem;
  padding-right: 1rem;
}

.length-btn {
  padding: 0 .5rem;
  border-radius: 3px;
  font-size: 110%;
  border: 1px solid #444;
}

.timer-div {
  grid-row: 2;
  grid-column: 1/3;
  justify-self: center;
}

.timer-div-head {
  border: 1px solid #444;
  border-radius: 3px;
  text-align: center;
  padding: 1.2rem 6rem 1.8rem 6rem;
}

.timer-cycle {
  font-weight: 500;
  margin-top: 2rem;
}

.timer-div-buttons {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
}

.timer-div-button {
  margin-left: .5rem;
  margin-right: .5rem;
  padding: .2rem .4rem;
}


@media(max-width: 1070px) {
  .container {
      width: 60%;
  }
}

@media(max-width: 992px) {
  .container {
      width: 70%;
  }
}

@media(max-width: 720px) {
  .container {
      padding: 1rem 2rem;
  }
}

@media(max-width: 625px) {
  .container {
      width: 95%;
      padding: 1rem 1rem;
  }

  .container-divs {
      grid-gap: 3rem 1rem;
  }

  .length-head {
      font-size: 100%;
  }

  .length-div {
      border: 1px solid #444;
      padding: 1.5rem .5rem 2rem .5rem;
      border-radius: 3px;
  }
  
}

@media(max-width: 349px) {
  .container {
      padding: 1rem .5rem;
      margin: 3rem auto;
  }

  .length-div {
      border: 1px solid #444;
      padding: 1.5rem .3rem 2rem .3rem;
      border-radius: 3px;
  }
}